html, body {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  width: 100vw;
  height: 100vh;
  flex-direction: row;
  display: flex;
}

.slider {
  transform: translateY(5px);
}

.container > .left-panel {
  float: left;
  height: 100%;
  flex-direction: column;
  flex: auto;
  padding: 10px 10px 0;
  display: flex;
}

.container > .left-panel > * {
  margin-bottom: 10px;
}

.container > .left-panel > :last-child {
  margin-bottom: 0;
}

.container > .left-panel > #editor {
  flex: 2 0 50px;
}

.container > .left-panel > #memory {
  flex: 2 0 50px;
  display: block;
}

.container > .left-panel > #state {
  flex: none;
  display: block;
}

.container > #diagram {
  float: right;
  flex: none;
}

#editor, #memory {
  background-color: azure;
  border: 1px solid silver;
  border-radius: 10px;
  display: block;
}

#memory {
  font-family: Fira Code, Roboto Mono, monospace;
  overflow: scroll;
}

#memory table {
  margin: 0 auto;
}

#memory td {
  transition: background-color 1s;
}

#memory td.zero {
  color: #a9a9a9;
}

#memory td.changed {
  background-color: #7fff00;
}

#diagram {
  max-height: 100vh;
}

#state {
  background-color: #deb887;
  border: 1px solid #111;
  padding: 2px;
  font-family: Fira Code, Roboto Mono, monospace;
}

